body {
  font-family: 'Carlito';
  background-color: #fff;
  font-weight: bold;
}

.wrapper {
  max-width: 1180px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

@media screen and (max-width: 780px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}

input::-webkit-input-placeholder {
  text-indent: 0;
  transition: text-indent 0.4s ease;
}

input::-moz-placeholder {
  text-indent: 0;
  transition: text-indent 0.4s ease;
}

input:-moz-placeholder {
  text-indent: 0;
  transition: text-indent 0.4s ease;
}

input:-ms-input-placeholder {
  text-indent: 0;
  transition: text-indent 0.4s ease;
}

input:focus::-webkit-input-placeholder {
  text-indent: 600px;
  transition: text-indent 0.4s ease;
}

input:focus::-moz-placeholder {
  text-indent: 600px;
  transition: text-indent 0.4s ease;
}

input:focus:-moz-placeholder {
  text-indent: 600px;
  transition: text-indent 0.4s ease;
}

input:focus:-ms-input-placeholder {
  text-indent: 600px;
  transition: text-indent 0.4s ease;
}

.title h2 {
  font-size: 33px;
  color: #343434;
}

@media screen and (max-width: 780px) {
  .title h2 {
    font-size: 24px;
  }
}

@media screen and (max-width: 780px) {
  .title h2 {
    text-align: center;
  }
}

.title h2 span {
  color: #d0d0d0;
  font-weight: normal;
  font-size: 24.5px;
}

h3 {
  font-size: 24.5px;
  color: #343434;
}

h3 span {
  color: #d0d0d0;
  font-weight: normal;
  font-size: 24.5px;
}

.show-more {
  padding-top: 30px;
}

.show-more a {
  color: #506aa1;
  font-size: 16px;
  text-decoration: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.13);
}

.show-more a:hover {
  opacity: .4;
  transition: all .3s;
}

#header {
  background: url(../images/bgc-img.png) no-repeat center center;
  background-size: cover;
  height: 558px;
}

@media screen and (max-width: 780px) {
  #header {
    background: url(../images/bgc-img.png) no-repeat 80% 80%;
  }
}

#header .container-header {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 30px;
}

@media screen and (max-width: 560px) {
  #header .container-header {
    justify-content: space-between;
  }
}

#header .container-header .mobile-menu-toggle {
  display: none;
}

@media screen and (max-width: 960px) {
  #header .container-header .mobile-menu-toggle {
    display: inline-block;
    font-size: 50px;
    text-decoration: none;
    color: #ffffff;
  }
}

@media screen and (max-width: 960px) {
  #header .container-header .menu {
    display: none;
  }
}

#header .container-header .menu #hider {
  position: absolute;
  top: -9999px;
  left: -9999px;
}

#header .container-header .menu #hider:checked + .content {
  display: block;
}

#header .container-header .menu .content {
  position: absolute;
  display: none;
}

#header .container-header .menu ul {
  list-style: none;
  display: flex;
  align-items: center;
}

#header .container-header .menu ul li {
  padding: 0 15px;
}

#header .container-header .menu ul li a {
  text-decoration: none;
  color: #ffffff;
  font-size: 16px;
}

#header .container-header .menu ul li a:hover {
  color: #ffd800;
  transition: all .4s;
}

@media screen and (max-width: 960px) {
  #header .container-header .menu ul li a:hover {
    color: #ffffff;
  }
}

#header .container-header .menu ul li #more {
  color: #ffffff;
  border-bottom: 1px dotted #ffffff;
  cursor: pointer;
}

#header .container-header .menu ul li #more:hover {
  color: #ffd800;
  border-bottom: 1px dotted #ffd800;
  transition: all .4s;
}

@media screen and (max-width: 960px) {
  #header .container-header .menu ul li #more:hover {
    color: #ffffff;
  }
}

#header .container-header .menu ul li > ul li {
  padding: 5px 0;
}

#header .container-header .menu.is-open {
  display: block;
  position: absolute;
  width: 100%;
  top: 135px;
  margin-left: -30px;
  padding-right: 30px;
  padding-left: 30px;
  background-color: #ffd800;
}

#header .container-header .menu.is-open ul li .content {
  display: none;
  position: inherit;
}

@media screen and (max-width: 960px) {
  #header .container-header .menu.is-open {
    top: 90px;
  }
}

@media screen and (max-width: 780px) {
  #header .container-header .menu.is-open {
    top: 90px;
    margin-left: -15px;
    padding-right: 15px;
    padding-left: 15px;
  }
}

@media screen and (max-width: 560px) {
  #header .container-header .menu.is-open {
    top: 135px;
  }
}

#header .container-header .menu.is-open ul {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#header .container-header .menu.is-open ul li {
  width: 100%;
  text-align: center;
  padding: 6px 0 6px;
}

#header .search {
  display: flex;
  align-items: center;
}

#header .search #search {
  background: none;
  border: none;
  outline: none;
  padding-left: 10px;
  color: white;
  width: 120px;
}

#header .search #search::-webkit-input-placeholder {
  color: rgba(209, 193, 174, 0.7);
}

#header .search #search::-moz-placeholder {
  color: rgba(209, 193, 174, 0.7);
}

#header .search #search:-moz-placeholder {
  color: rgba(209, 193, 174, 0.7);
}

#header .search #search:-ms-input-placeholder {
  color: rgba(209, 193, 174, 0.7);
}

@media screen and (max-width: 560px) {
  #header .account.is-open-account {
    width: 100%;
    position: absolute;
    top: 86px;
    background-color: #ffd800;
    margin-left: -15px;
    padding-right: 15px;
    padding-left: 15px;
  }
  #header .account.is-open-account .ul-account {
    text-align: center;
    display: flex;
    flex-direction: column;
  }
  #header .account.is-open-account .ul-account li:last-child {
    padding-top: 12px;
    padding-bottom: 6px;
  }
}

#header .account .ul-account {
  display: flex;
  justify-content: space-between;
  font-size: 16px;
}

@media screen and (max-width: 560px) {
  #header .account .ul-account {
    display: none;
  }
}

#header .account .ul-account .sign-up {
  padding-right: 38px;
}

@media screen and (max-width: 780px) {
  #header .account .ul-account .sign-up {
    padding-right: 15px;
  }
}

@media screen and (max-width: 560px) {
  #header .account .ul-account .sign-up {
    padding-right: 0;
  }
}

#header .account .ul-account a {
  text-decoration: none;
  color: #ffffff;
}

#header .account .ul-account a:hover {
  color: #ffd800;
  transition: all .4s;
}

.container-banner {
  padding-top: 150px;
  max-width: 770px;
}

.container-banner h1 {
  font-size: 48px;
  color: #ffffff;
}

@media screen and (max-width: 780px) {
  .container-banner h1 {
    font-size: 36px;
  }
}

@media screen and (max-width: 560px) {
  .container-banner h1 {
    font-size: 28px;
  }
}

.container-banner .buttons {
  padding-top: 49px;
}

