.container-banner .buttons #sell-pet, .container-banner .buttons #have-a-pet {
  background: none;
  border-radius: 4px;
  color: #ffd800;
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
  border: 2px solid #ffd800;
  padding: 19px 70px 14px;
}

